import React from "react"
import Layout from "../components/layout"
import ShowGrid from "../components/ShowGrid"

const ShowTemplate = ({ pageContext }) => {
  const { show, language, videos } = pageContext

  if (!videos || videos.length === 0) {
    return (
      <Layout>
        <h1>
          {show.text} - {language}
        </h1>
        <p>No videos found for this show in {language}.</p>
      </Layout>
    )
  }

  const showData = videos.map(video => ({
    id: video.id.videoId,
    title: video.snippet.title,
    thumbnail: video.snippet.thumbnails.high.url,
  }))

  return (
    <Layout>
      <h1>
        {show.text} - {language}
      </h1>
      <ShowGrid shows={showData} />
    </Layout>
  )
}

export default ShowTemplate
