import React from "react"

const ShowCard = ({ show, isPlaying, onClick }) => {
  const { id, title, thumbnail } = show

  return (
    <div onClick={onClick} style={{ cursor: "pointer" }}>
      {isPlaying ? (
        <iframe
          title={title}
          src={`https://www.youtube.com/embed/${id}?autoplay=1&rel=0&showinfo=0`}
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
          style={{
            width: "100%",
            height: "auto",
            minHeight: "200px",
            maxHeight: "100%",
          }}
        />
      ) : (
        <img
          src={thumbnail}
          alt={title}
          style={{
            width: "100%",
            height: "auto",
            minHeight: "200px",
            maxHeight: "100%",
          }}
        />
      )}
    </div>
  )
}

export default ShowCard
